<template>
  <div class="long-text-page">
    <!-- 头部 文章标题 -->
    <div class="doc-tit-box">
      <div class="doc-tit-content">详情页长文本功能测试组件</div>
    </div>
    <!-- 文本区域（左侧列白，右侧文本） -->
    <div class="content-box">
      <!-- 左侧列表 -->
      <div class="key-word-list">
        <div class="tab-nav"></div>
        <ul class="text-list">
          <p class="no-key-tips" v-if="data.catalogue.length == 0">
            文中暂无符合的关键词
          </p>
          <li
            class="text-list-item"
            v-for="item in data.catalogue"
            :key="item.id"
            v-html="item.content"
            @click="switchHighlight($event)"
          ></li>
        </ul>
      </div>
      <!-- 右侧长文本 -->
      <div class="long-text-box">
        <div class="loading"  v-show="data.isLoading">
          <p class="loading-icon iconfont icon-sousuo"></p>
          <p id="LoadAnimation"></p>
        </div>
        <!-- 长文本头部搜索 -->
        <div class="text-title-box">
          <input
            class="query-input"
            type="text"
            placeholder="请输入关键词在正文中搜索"
            v-model="data.queryKeyWord"
            @keydown.enter="startQuery"
          />
          <p class="query-btn iconfont icon-sousuo" @click="startQuery"></p>
          <!-- 类型 -->
          <div class="querytype">
            <input
              class="checkbox-type"
              id="t1"
              type="checkbox"
              @click="
                data.queryType =
                  data.queryType === data.RETRIEVAL_TYPE.paragraph
                    ? data.RETRIEVAL_TYPE.piece
                    : data.RETRIEVAL_TYPE.paragraph
              "
              :checked="data.queryType === data.RETRIEVAL_TYPE.paragraph"
            />
            <label for="t1">同段</label>
            <input
              class="checkbox-type"
              id="t2"
              type="checkbox"
              @click="data.queryType = data.queryType === '2' ? '0' : '2'"
              :checked="data.queryType === '2'"
            />
            <label for="t2">同句</label>
          </div>
          <!-- 切换 -->
          <div class="key-word-position">
            <span
              class="iconfont icon-qiehuanqizuo"
              @click="switchHighlight('prev')"
            ></span>
            <span>{{ data.Knum }} / {{ data.Ktotal }}</span>
            <span
              class="iconfont icon-qiehuanqiyou"
              @click="switchHighlight('next')"
            ></span>
          </div>
        </div>

        <!-- 长文本 -->
        <div class="long-text">
          <div
            class="center"
            id="longCenter"
            v-html="data.longTextContent"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, getCurrentInstance, onMounted } from 'vue';
import Clipboard from 'clipboard';
import { RETRIEVAL_TYPE } from './longTextConst.js';
// import { swh1 } from './minText.js';
// import { swh1 } from './LongText.js';
import { swh1 } from './bigText.js';

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      RETRIEVAL_TYPE,
      keyWordBCol: 'red', //背景颜色
      keyWordSCol: '#fff', //字体颜色
      oldEle: null, //保存旧的元素
      Knum: 0, //当前第几个关键词
      Ktotal: 0, //搜索的全部关键词个数
      listNumId: 0, //列表每一项标签id属性

      textCenter: '', // 除去标签的内容(高亮段落使用)
      longTextContent: '', // 渲染展示文本内容(高亮时不会改变，用于重置可以)
      centerEle: null, // 长文本dom元素（用户循环改变文本高亮）

      queryKeyWord: '公司 上市 产品 股', // 搜索关键词    证券 二次
      oldQueryKeyWordReg: '', //保留上一次正则字符串
      queryKeyWordArr: [], //关键词数组格式
      queryKeyWordReg: '', // 用于正则的查询格式     证券|二次
      keyWordIndex: [], // 保存所有关键词下标位置
      catalogue: [], // 关键词对应段落目录
      queryType: RETRIEVAL_TYPE.piece, // 0同篇  1同段  2同句
      oldQueryType: '', // 保留上一次查询类型
      keyPLength: 30, //截取关键词前后字符长度

      copyButton: null, //表格复制按键元素
      isLoading: false, //控制等待
      isHaveKey: false //是否有高亮词
    });
    // 挂在后获取相关元素
    onMounted(() => {
      data.centerEle = document.getElementById('longCenter'); // 获取长文本容器dom元素
      data.textCenter = data.centerEle.innerText; // 除去标签的原始内容
      copyTable(); //表格复制
    });
    // 获取表格遍历添加复制按键，添加按键复制事件
    const copyTable = () => {
      // 全部table元素转化为真数组
      let tableEleAll = Array.from(document.getElementsByTagName('table'));
      // 遍历全部table元素
      tableEleAll.forEach((itemTable, index) => {
        // 移入表格创建复制按键，增加点击事件
        itemTable.onmouseenter = function (e) {
          data.copyButton = document.createElement('button'); //保存按键元素移除时使用
          data.copyButton.className = 'copy';
          data.copyButton.innerHTML = '复制表格';
          const currentEle = e.target; //要复制的表格元素
          currentEle.appendChild(data.copyButton); //复制按键添加到表格
          // 点击复制按键，设置当前表格为复制目标
          data.copyButton.onclick = new Clipboard('.copy', {
            // 通过target指定要复印的节点
            target: function () {
              //点击时清除复制按键，解决复制带有复制表格字样
              itemTable.removeChild(itemTable.lastChild);
              // 复制时设置table属性宽度，解决复制到word中宽度超出
              currentEle.width = '98%';
              return currentEle;
            }
          });
          //复制成功
          data.copyButton.onclick.on('success', function (e) {
            emptySelectText();
            // 解决复制选中高亮（清除页面选中文字）
            myTips('success', '复制成功~');
            data.copyButton.onclick.destroy();
          });
          // 复制失败
          data.copyButton.onclick.on('error', function (e) {
            emptySelectText();
            // 解决复制选中高亮（清除页面选中文字）
            myTips('error', '复制失败~');
            data.copyButton.onclick.destroy();
          });
        };
        // 移除时清除按键事件和按键元素
        itemTable.onmouseleave = function () {
          data.copyButton.onclick.destroy();
          itemTable.removeChild(itemTable.lastChild);
        };
      });
    };
    // 清空页面选中文字
    const emptySelectText = () => {
      window.getSelection
        ? window.getSelection().removeAllRanges()
        : document.selection.empty();
    };
    // 初始化长文本内容（获取对象中文本拼接字符串）
    const initContent = () => {
      let pageContentArr = [];
      swh1.forEach((item) => {
        pageContentArr.push(item.pageContent);
      });
      data.longTextContent = pageContentArr.join(''); // 可添加高亮内容
    };
    initContent();
    // 触发执行关键词查询（每次查询初始化，再执行新的查询）
    const startQuery = () => {
      const absQueryKeyWord = data.queryKeyWord.trim(); //关键词去除前后空格
      if (absQueryKeyWord) {
        // 有搜索内容
        data.queryKeyWordArr = absQueryKeyWord
          .split(' ')
          .filter((item) => item.length); //获取关键词数组，删除空字符串（不然会卡死）
        data.queryKeyWordReg = data.queryKeyWordArr.join('|'); //数组，再以 | 拼接（正则查询格式）
        // 查询正则和类型都没变化就不执行新检索操作
        if (
          data.oldQueryKeyWordReg === data.queryKeyWordReg &&
          data.oldQueryType === data.queryType
        )
          return;
        // 执行查询前
        data.oldQueryType = data.queryType; //备份此次搜索类型
        data.oldQueryKeyWordReg = data.queryKeyWordReg; //备份此次搜索词
        resetData(); //内容不同 ,有高亮重置
        data.isLoading = true; //等待提示
        setTimeout(() => {
          console.time('检索时间');
          setHighlight(); // 执行高亮逻辑,放在定时器才能显示loading
          console.timeEnd('检索时间');
        }, 0);
      } else {
        resetData(); //无内容  有高亮重置
      }
    };
    //（速度优化） 清除长文本中所有高亮关键词
    const emptyAllHigh = () => {
      // 找到有高亮内容的外层包裹标签
      let temporarySpanAll = Array.from(
        document.getElementsByClassName('temporarySpan')
      );
      temporarySpanAll.forEach((itemNode) => {
        // 最佳清空方式：k速度优化++ 7196个标签 308.1728515625 ms 循环所有的临时temporarySpan，替换内容中class="swhKey"高亮标签（这样不用重置table复制）
        itemNode.innerHTML = itemNode.innerHTML.replace(
          /<span class="swhKey"(?:.*?)>([\s\S]*?)<\/span>/g,
          '$1'
        );
        // 优化前方式1：使用节点替换节点内容（最慢）7196个标签 10559.537841796875 ms
        // itemNode.parentNode.replaceChild(
        //   document.createTextNode(itemNode.innerText),
        //   itemNode
        // );
      });
      // 优化前方式2：直接替换整个文本，速度快，但是需要重置table复制 7196个标签 222.956787109375 ms
      // data.centerEle.innerHTML = data.longTextContent; //重置内容（上面清除全部高亮）
      // copyTable(); //重置表格复制按键
      // 优化前方式3：倒找高亮标签只删除标签上的高亮属性（不可理，会残留很多无用标签）
    };
    // 有高亮重置
    const resetData = () => {
      if (data.isHaveKey) {
        setTimeout(() => {
          console.time('重置时间');
          emptyAllHigh(); //清除全部高亮   速度慢
          console.timeEnd('重置时间');
          data.oldEle = null; //清除保存旧的高亮元素
          data.catalogue = []; //重置关键词段落数组
          data.keyWordIndex = []; //重置关键词下标
          data.Knum = 0; //重置当前高亮个数
          data.Ktotal = 0; //重置当前关键词总个数
          data.listNumId = 0; //重置列表计数
          data.isHaveKey = false; //重置无高亮词
        }, 0);
      }
    };
    //（长文本）点击切换当前高亮关键词（上下切换关键词）
    const switchHighlight = (type) => {
      // 总数小于1不可点击切换
      if (data.Ktotal < 1) return;
      // 上下切换逻辑
      if (type === 'next') {
        //下一个
        data.Knum = data.Knum === data.Ktotal ? 1 : ++data.Knum;
      } else if (type === 'prev') {
        //上一个
        data.Knum = data.Knum <= 1 ? data.Ktotal : --data.Knum;
      } else {
        // 列表点击逻辑，同篇同段 获取段落第一个子id或者点击子本身直接id
        const targetID = type.target.children[0] || type.target;
        // 截取位置下标
        data.Knum = targetID.id.split('_')[1];
      }
      // 通过id切换高亮和跳转位置
      clickPieceList();
    };
    //（长文本）上下切换逻辑（和同篇时点击列表逻辑）（同段同句点击不可使用）
    const clickPieceList = () => {
      let ele = document.querySelector(`#key_${data.Knum}`);
      // 清除上一个元素的样式
      if (data.oldEle) {
        data.oldEle.style.backgroundColor = 'transparent'; // 清除背景色
        data.oldEle.style.color = data.keyWordBCol; // 重置字体高亮
      }
      // 给当前元素增加样式
      ele.style.backgroundColor = data.keyWordBCol; // 设置背景高亮
      ele.style.color = data.keyWordSCol; // 设置当前字体白色
      // 定位到元素位置
      ele.scrollIntoView(true);
      // 保存当前元素,下次成为旧元素
      data.oldEle = ele;
    };
    //（长文本）（速度优化）***主要高亮逻辑***    传入dom元素，遍历节点，文本节点查找关键词替换高亮，标签节点递归
    const forDomGetHigh = (root) => {
      //===================  不需要频繁操作整个dom 速度明显提高(提升100倍速度  k速度优化++6000ms-60ms
      for (let i = 0; i < root.childNodes.length; i++) {
        // 当前节点
        const currentNode = root.childNodes[i];
        // 节点文本
        const nodeText = currentNode.nodeValue;
        if (currentNode.nodeType == 1) {
          //标签节点 直接队递归
          forDomGetHigh(currentNode);
        } else if (currentNode.nodeType == 3) {
          // 同句   空格\n停止,table中问题
          if (
            data.queryType === RETRIEVAL_TYPE.sentence &&
            !nodeText.includes('\n')
          ) {
            // 执行同句高亮(当前节点 ， 节点内容)
            withSentence(currentNode, nodeText);
          } else {
            // 同篇 同段
            // 无论文本中是否包含关键词都会执行替换（优化  可先判断是否包含再替换  感觉是一样的，replace也是判断包含再替换）
            // 直接段落节点替换
            const highKeyStr = nodeTextHigh(nodeText);
            //嵌套标签需要下面判断，
            // *****判断后的值和之前的值是否一致，一致代表不需要更改，不一致代表有高亮关键词
            if (highKeyStr !== nodeText) {
              replaceNode(currentNode, highKeyStr);
            }
          }
        }
      }
      //===================

      // // 以下是之前逻辑  只能捕获有标签的文本  不能匹配没有标签的文本（所以改成判断节点）
      // ================   操作dom过多查询慢
      // const children = root.children;
      // // 同篇
      // if(children.length){
      //   for (var i = 0; i < children.length;i++){
      //    children[i].children&&forDomGetHigh(children[i])
      //   }
      // }else {
      //     // KW: 当前搜索的关键词本身
      //     // k1: 关键词查找的位置下标
      //     // 同段
      //     // 同篇查找高亮    多分支正则    证券|二次  关键词数组转为 | | 拼接data.queryKeyWordReg
      //     root.innerHTML= root.innerText.replace(new RegExp(data.queryKeyWordReg, "g"), (KW) => {
      //         //关键词下标保存
      //         data.Ktotal++;    //关键词总数量
      //         return `<span id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
      //     });
      // }
      // =================
      // (每次查询都会重置整个文本html所以要从新创建复制按键，现在修改为删除之前高亮标签属性，不用再次设置表格)
    };
    //（长文本）同句单独逻辑
    const withSentence = (currentNode, nodeText) => {
      // 分割每一句  举例1: 搜索 孙 文 aaa孙a文aa。s孙a文hdddd;ccccc;hhhhs孙a文h
      const sentenceArr = nodeText.split(new RegExp('[。；;]'));
      // 获取过滤符合条件的数组  举例1: [aaa孙a文aa,s孙a文hdddd ,hhhhs孙a文h]
      const satisfySentenceArr = sentenceArr.filter((itemSentence) => {
        // 排除空字符串停止
        if (!itemSentence) return false;
        // 当前句子是否包含所有关键词
        const bool = data.queryKeyWordArr.every((item) => {
          return itemSentence.includes(item);
        });
        // 包含就返回
        if (bool) return itemSentence;
      });
      // 将符合条件的关键词高亮
      satisfySentenceArr.forEach((itemSen) => {
        // 同句k列表追加
        data.catalogue.push({
          content: itemSen
        });
        // 每一句高亮
        const highKeyStr = nodeTextHigh(itemSen);
        // 文本替换（整个节点高亮）
        nodeText = nodeText.replace(itemSen, highKeyStr);
      });
      replaceNode(currentNode, nodeText);
    };
    //（长文本）节点文本高亮（用于长文本），后期可与列表文本组合
    const nodeTextHigh = (t) => {
      return t.replace(new RegExp(data.queryKeyWordReg, 'g'), (KW) => {
        data.Ktotal++; //替换次数（查找到的个数）
        // class="swhKey" 目前无用处（直接删除）
        return `<span class="swhKey" id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
      });
    };
    //（长文本） 高亮文本替换到节点中
    const replaceNode = (currentNode, highKeyStr) => {
      // 创建临时span包裹元素，设置内容，必须创建标签节点，文本节点不会以html形式展示<span>
      const temporarySpan = document.createElement('span');
      temporarySpan.innerHTML = highKeyStr; //设置高亮内容
      temporarySpan.className = 'temporarySpan'; //设置class(方便清除时使用)
      // 高亮文本替换之前文本，父节点执行替换（新节点，就节点）
      currentNode.parentNode.replaceChild(temporarySpan, currentNode);
      /**
       * 例如： <p>长文本渲染高亮替换逻辑</p>      查找的是 "渲染" 关键词
       * 处理后高亮：<p>
       *              替换以下整个包括元素（重置时直接删除temporarySpan）
       *             <span class="temporarySpan">
       *               "长文本"
       *              <span class="swhKey" id="key_1" style="color: rgb(255, 255, 255); padding: 0px 5px; background-color: red;">渲染</span>
       *               "高亮替换逻辑"
       *             </span>
       *            </p>
       *  */
    };

    //（全部）（速度优化）高亮匹配之前 k速度优化++ 缩短一半速度，对所有的一级dom进行过滤，存在关键词的在进行递归替换
    const forDomGetHighFilter = (root) => {
      const children = root.children;
      const oneEleText = []; //保存包含关键词的第一层节点
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        for (var i = 0; i < children.length; i++) {
          // 同篇逻辑  当前段落包含一个关键词就返回true
          const bool = data.queryKeyWordArr.some((item) => {
            return children[i].innerText.includes(item);
          });
          // 保存当前段落
          if (bool) {
            oneEleText.push(children[i]);
          }
        }
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        for (var i = 0; i < children.length; i++) {
          // 同段逻辑  当前段落包含所有关键词才返回true
          const bool = data.queryKeyWordArr.every((item) => {
            return children[i].innerText.includes(item);
          });
          // 保存同段包含的元素
          if (bool) {
            // 同段k列表追加 保存同段文本,段落id和段落文本（之后处理内容高亮列表展示）
            if (data.queryType === RETRIEVAL_TYPE.paragraph) {
              data.catalogue.push({
                content: children[i].innerText
              });
            }
            // 保存当前段落
            oneEleText.push(children[i]);
          }
        }
      }
      //对符合条件的节点进入递归 匹配高亮 （文章区域高亮）
      if (oneEleText.length) {
        oneEleText.forEach((item) => {
          forDomGetHigh(item);
        });
      }
    };
    //（全部）点击查询开始执行（匹配关键词高亮&&关键词列表生成）
    const setHighlight = () => {
      // 匹配高亮之前进行过滤提高速度（同篇不是用forDomGetHighFilter 直接使用 forDomGetHigh 检索速度变慢）
      forDomGetHighFilter(data.centerEle);
      // 未检索出关键词
      if (data.Ktotal) {
        data.isHaveKey = true; //有高亮词
        // 有检索关键词再执行 段落列表获取
        console.time('列表时间');
        getQueryList();
        console.timeEnd('列表时间');
      } else {
        myTips('error', '文档中无符合的关键词~');
      }
      data.isLoading = false;
    };
    //（列表）获取关键词段落列表（同篇 同段 同句）
    const getQueryList = () => {
      // 同篇段落获取
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        // 获取原始内容关键词位置(生成关键词段落列表数据)
        getOriginalIndex();
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        // 同段 同句 列表 处理段落时高亮
        data.catalogue.forEach((item) => {
          item.content = pureListTextHigh(item.content); //处理高亮
        });
      }
      // 搜索完成后定位至第一个关键词
      switchHighlight('next');
    };
    // 以下是同篇截取30逻辑3步
    //（列表）同篇 列表（1）  获取原始内容中关键词下标
    const getOriginalIndex = () => {
      // 去除标签内容获取关键词位置
      const regInter = eval(`/${data.queryKeyWordReg}/g`); //匹配正则
      const regExp = data.textCenter.matchAll(regInter); //匹配内容迭代器
      for (const item of regExp) {
        const keyPObj = {
          kWord: item[0], //查询的字符串
          index: item.index //查询的下标
        };
        data.keyWordIndex.push(keyPObj); //保存所有关键词下标
      }
      // 根据关键词下标，处理为前后30字符截取下标对象
      getHighIndex();
    };
    //（列表）同篇 列表（2） 根据关键词下标，处理为前后30字符截取下标对象，生成关键词段落数组
    const getHighIndex = () => {
      let handleIndex = [];
      for (let i = 0; i < data.keyWordIndex.length; i++) {
        // 前面位置，第一个就设置0开始，不是第一个就设置前面位置加字符长度
        const prevIndex = i
          ? data.keyWordIndex[i - 1].index +
            data.keyWordIndex[i - 1].kWord.length
          : 0;
        const currentIndex = data.keyWordIndex[i].index; //当前关键词位置
        //下一个关键词位置,最后一次不存在undefined （不需要特殊判断，截取查出长度自动截取到最后）
        const nextIndex = data.keyWordIndex[i + 1]?.index;
        //初始化开始截取位置，当前位置-30字符
        const initStartIndex = currentIndex - data.keyPLength;
        //初始化结束位置  ，当前位置+关键词长度+30字符
        const initEndIndex =
          currentIndex + data.keyWordIndex[i].kWord.length + data.keyPLength;
        let handleIndexObj = {
          // 开始位置小于上一个关键词位置，就设置上个位置，否则设置当前开始位置（前面关键词停止）
          startIndex: initStartIndex < prevIndex ? prevIndex : initStartIndex,
          // 结束位置大于下一个关键词位置，就设置到关键词位置，否则就设置当前结束位置
          // （后面关键词停止）undefined时始终按结束位置，超出截取到最后
          endIndex: initEndIndex > nextIndex ? nextIndex : initEndIndex
        };
        handleIndex.push(handleIndexObj); // 开始结束位置对象
      }
      highParagraphHandle(handleIndex); // 生成关键词段落数组
    };
    //（列表）同篇 列表（3）  根据开始结束位置，生成关键词段落列表
    const highParagraphHandle = (SEindexObj) => {
      SEindexObj.forEach((item, index) => {
        //  截取关键词前后字段
        let pCenter = data.textCenter.slice(item.startIndex, item.endIndex);
        // 创建段落对象id和高亮内容
        const itemKeyWordObj = {
          content: pureListTextHigh(pCenter) //处理高亮
        };
        //同篇k列表追加 保存关键词段落对象
        data.catalogue.push(itemKeyWordObj);
      });
    };
    //（列表）纯文本列表高亮（用于列表最后一步替每一条文本高亮）
    const pureListTextHigh = (itemContent) => {
      return itemContent.replace(
        new RegExp(data.queryKeyWordReg, 'g'),
        (KW, index) => {
          data.listNumId++;
          //  console.log(index);   这里获取同段中关键词位置（可以开发段落长度截取，不显示显示整个段落）
          return `<span id="list_${data.listNumId}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`; // 内容处理关键词高亮
        }
      );
    };
    // 提示浮窗
    const myTips = (type, text) => {
      proxy.h_tips({
        type: type, //success   error
        msg: text,
        top: 30,
        time: 1000
      });
    };
    return {
      data,
      startQuery,
      switchHighlight
    };
  }
};
</script>

