    "pageContent": "<p id=\"tag_p_3555\" istitle>（5） 支付给职工以及为职工支付的现金</p><p id=\"tag_p_3556\">报告期内，公司支付给职工以及为职工支付的现金金额分别为4,628.18万元、5,467.00万元和8,323.82万元，与公司员工人数规模及变动趋势相匹配。</p><p id=\"tag_p_3557\" istitle>（6） 支付的各项税费</p><p id=\"tag_p_3558\">报告期内，公司支付的各项税费金额分别为2,315.12万元、2,681.74万元和3,706.38万元，主要为增值税、企业所得税等。</p><p id=\"tag_p_3559\" istitle>（7） 支付其他与经营活动有关的现金</p><p id=\"tag_p_3560\">报告期内，公司支付其他与经营活动有关的现金金额分别为1,125.85万元、1,516.72万元和951.02万元，主要系支付费用和往来款，具体情况如下：</p><p id=\"tag_p_3561\">单位：万元</p><table cellspacing=\"0\" l1=\"7\" border=\"1\" iscatalog=\"true\" id=\"tag_table_511\"> \n <tbody l1=\"7\"> \n  <tr style=\"height:21pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20169\">项目</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20170\">2021 年度</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20171\">2020 年度</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20172\">2019 年度</td> \n  </tr> \n  <tr style=\"height:20pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20173\">费用</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20174\">706.32</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20175\">1,218.70</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20176\">977.37</td> \n  </tr> \n  <tr style=\"height:20pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20177\">往来款</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20178\">22.42</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20179\">260.61</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20180\">79.73</td> \n  </tr> \n  <tr style=\"height:20pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20181\">押金及保证金</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20182\">122.92</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20183\">14.92</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20184\">32.74</td> \n  </tr> \n  <tr style=\"height:20pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20185\">代收代付款</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20186\">75.79</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20187\">-</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20188\">-</td> \n  </tr> \n  <tr style=\"height:20pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20189\">其他</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20190\">23.56</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20191\">22.49</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20192\">36.01</td> \n  </tr> \n  <tr style=\"height:21pt\" l1=\"7\"> \n   <td style=\"width:138pt;\" l1=\"7\" id=\"tag_td_20193\">合计</td> \n   <td style=\"width:116pt;\" l1=\"7\" id=\"tag_td_20194\">951.02</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20195\">1,516.72</td> \n   <td style=\"width:86pt;\" l1=\"7\" id=\"tag_td_20196\">1,125.85</td> \n  </tr> \n </tbody> \n</table><p id=\"tag_p_3562\" istitle>（8） 经营现金流与净利润差异分析</p><p id=\"tag_p_3563\">报告期内，公司经营活动产生的现金流量与净利润的关系如下：</p>"
